.blog-header {
    padding: 0 0 10px;
    margin-bottom: 8px;
    border-bottom: 1px solid #dddddd;
}

.blog-header h1{
    font-weight: bold;
    font-size: 25px;
    margin-bottom: 10px;
}

.blog-header .user{
    color: green;
    font-weight: bold;
}
.blog-header .time{
    margin-left: 10px;
    font-size: 12px;
}


